import { Pipe, PipeTransform } from '@angular/core';

/**
 * 
 * let message = " {0} {1}"
 * template:
 * <h1>{{ message| replaceX: '', '' }}</h1>
 * resule:
 * <h1>  </h1>
 */
@Pipe({name: 'replaceX'})
export class UploadReplaceXPipe implements PipeTransform {
    transform(value: any, ...args: any[]) {
        args.forEach((v, i) => {
            value = value.replace(`{${i}}`, v);
        });

        return value;
    }
}