import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
// normal
export type UploadProgressStatusType = 'success' | 'error' | 'active' | 'normal';

// const statusColorMap = new Map([
//   ['normal', '#388fff'],
//   ['exception', '#f24645'],
//   ['success', '#51bd78']
// ]);
@Component({
  selector: 'upload-progress',
  templateUrl: './upload-progress.component.html',
  styleUrls: ['./upload-progress.component.css']
})
export class UploadProgressComponent implements OnInit, OnChanges {
  //
  @Input() showInfo: boolean = true;
  // 'normal'
  @Input() status: UploadProgressStatusType;
  //
  @Input() successPercent: number;
  @Input() percent = 0;
  @Input() maxWidth =0;
  //
  private cachedStatus: UploadProgressStatusType = 'normal';
  //
  private inferredStatus: UploadProgressStatusType = 'normal';

  //
  get progressStatus(): UploadProgressStatusType {
    return this.status || this.inferredStatus;
  }
  constructor() { }
  ngOnChanges(changes: SimpleChanges): void {
    const { status, percent, successPercent } = changes;
    //
    if (status) {
      this.cachedStatus = this.status || this.cachedStatus;
    }
    //  
    if (percent || successPercent) {
      const fillAll = parseInt(this.percent.toString(), 10) >= 100;
      if (fillAll) {
        if ((this.isNotNil(this.successPercent) && this.successPercent! >= 100) || this.successPercent === undefined) {
          this.inferredStatus = 'success';
          // this.status = this.inferredStatus;
        }
      } else {
        this.inferredStatus = this.cachedStatus;
      }
      this.status = this.inferredStatus;
    }
  }
  ngOnInit() {
  }

  isNotNil<T>(value: T): value is NonNullable<T> {
    return typeof value !== 'undefined' && value !== null;
  }

}
